<template>
  <div class="WarnMessageWindow"
  v-if="WarnWindowDisplay"
  :style="{
    top:`${position.top}px`,
    left:`${position.left}px`,
  }"
  @mousedown="startDrag">
    <div class="control">
      <div class="left">     
        <div class="gameName">Cat Game Warn </div>        
      </div>
      <div class="right">
        <div class="controlButton close">
          <button type="button" @click="closeWarnWindow">
            <svg xmlns="http://www.w3.org/2000/svg" height="10" width="7.5" viewBox="0 0 384 512"><path d="M376.6 84.5c11.3-13.6 9.5-33.8-4.1-45.1s-33.8-9.5-45.1 4.1L192 206 56.6 43.5C45.3 29.9 25.1 28.1 11.5 39.4S-3.9 70.9 7.4 84.5L150.3 256 7.4 427.5c-11.3 13.6-9.5 33.8 4.1 45.1s33.8 9.5 45.1-4.1L192 306 327.4 468.5c11.3 13.6 31.5 15.4 45.1 4.1s15.4-31.5 4.1-45.1L233.7 256 376.6 84.5z"/></svg>
          </button>
        </div>
      </div>
    </div>

    <div class="WarnValue">
      <div class="WarnDiv">
        <div class="WarnImage">
          <img src="image/warnIocn.png">
        </div>
        <div class="WarnMessage">這是個還在開發的貓遊戲，點下Ok開始遊戲</div>
      </div>
      <div class="buttonDiv">
        <button type="button" @click="closeWarnWindow">OK</button>
      </div>
    </div> 
  </div>
</template>

<script>
import { onMounted, onUnmounted, ref } from 'vue';

export default {
  setup(){
    const position =ref({ top:window.innerHeight / 3, left:window.innerWidth  / 2 });
    const dragging = ref(false);
    const offset = ref({ x:0, y:0 });
    const isSmallScreen = ref(false);

    // 是否螢幕寬度小於 700px    
    const checkScreenSize = () =>{
      isSmallScreen.value = window.innerWidth < 786;
      const WarnMessageWindowElement = document.querySelector('.WarnMessageWindow');
  if (!WarnMessageWindowElement) {
    return; // 如果不存在，直接返回，避免錯誤
  }
  
      const WarnMessageWindowRect = document.querySelector('.WarnMessageWindow').getBoundingClientRect();
      if(!WarnMessageWindowRect){
        return;
      }
      if(isSmallScreen.value){
        position.value = {
          top: window.innerHeight / 2 - WarnMessageWindowRect.height / 2,
          left: window.innerWidth / 2 - WarnMessageWindowRect.width / 2,
        }
      }      
    }
    //開始移動
    const startDrag = (event)=>{
      if(!event.target.closest('.control')){
        return;
      }
      if(isSmallScreen.value) return;
      dragging.value = true;
      offset.value = {
        x:event.clientX - position.value.left,
        y:event.clientY - position.value.top,
      };
      window.addEventListener('mousemove', onDrag);
      window.addEventListener('mouseup', stopDrag);
    }
    //計算移動位置
    const onDrag = (event)=>{
      if(!dragging.value) return;
      const parentRect = document.querySelector('.backgronddIV').getBoundingClientRect();
      const WarnMessageWindowRect = document.querySelector('.WarnMessageWindow').getBoundingClientRect();
      // 限制范围
      const minX = parentRect.left;
      const maxX = parentRect.right - WarnMessageWindowRect.width;
      const minY = parentRect.top;
      const maxY = parentRect.bottom - WarnMessageWindowRect.height - 36;

      const newLeft = Math.min(Math.max(event.clientX - offset.value.x, minX), maxX);
      const newTop = Math.min(Math.max(event.clientY - offset.value.y, minY), maxY);

      position.value = {
        left: newLeft,
        top: newTop,
      }
    }
    //停止移動
    const stopDrag = ()=>{
      dragging.value = false;
      window.removeEventListener('mousemove', onDrag);
      window.removeEventListener('mouseup', stopDrag);
    }
    onMounted(()=>{
      checkScreenSize();
      window.addEventListener('resize', checkScreenSize);
    });
    onUnmounted(()=>{
      window.removeEventListener('resize', checkScreenSize);
    });

    //關閉視窗
    const WarnWindowDisplay = ref(true);
    const closeWarnWindow  = ()=>{
      WarnWindowDisplay.value = false;
    }
    return{
      position,
      startDrag,
      closeWarnWindow,
      WarnWindowDisplay,
    }
  }
}  
</script>

<style lang="scss" scoped>
*{
  padding: 0;
}
$normalWindowColor:#c0c7c8;
$hoverWindowColor:#dcdcdc;
$borderColor:#87888f;
$Win95Blue:#0000a8;

.WarnMessageWindow::after{  
  position: absolute;
  content: '';
  top: -2px;
  left: -2px;
  right: -2px;
  bottom: -2px;
  border-top: 1px solid $normalWindowColor;
  border-left: 1px solid $normalWindowColor;
  border-bottom: 1px solid #000;
  border-right: 1px solid #000;
  z-index: -1;
}
.WarnMessageWindow{  
  position: relative;
  display: block;
  background-color: $normalWindowColor;
  padding: 2px;
  width: auto;
  min-width: 300px;
  height: auto;
  box-sizing: border-box;
  border-top: 1px solid #fff;
  border-left: 1px solid #fff;
  border-bottom: 1px solid $borderColor;
  border-right: 1px solid $borderColor;

  .control{
    display: flex;
    height: 28px;
    justify-content: space-between;
    align-items: center;
    background-color: $Win95Blue;

    .left{
      display: flex;
      margin: 4px;
      box-sizing: border-box;

      .logo{
        height: 18px;
        width: 18px;
        margin-right: 4px;

        img{
          height: 100%;
          width: 100%;
          object-fit: cover;
        }
      }
      .gameName{
        font-weight: bolder;
        color: #fff;
      }
    }
    .right{
      display: flex;
      margin: 4px;
      box-sizing: border-box;

      .close{
          margin-left: 4px;
        }              
      .controlButton{
        
        button{
          height: 18px;
          width: 18px;
        }
      }
    }
  }
  .WarnValue{
    display: flex;
    flex-direction: column;
    row-gap: 12px;
    padding: 12px;
    box-sizing: border-box;

    .WarnDiv{
      display: flex;
      column-gap: 12px;
      height: auto;
      width: auto;
      
      .WarnImage{
        height: 32px;
        widows: 32px;

        img{
          object-fit: cover;
          height: 100%;
          width: 100%;
        }
        .WarnMessage{
          height: 100%;
          width: 100%;
        }
      }
    }
    .buttonDiv{
      display: flex;
      justify-content: center;

      button{
        padding:  4px 32px;
      }
    }
  }    
}
@media (max-width:1080px) {
  
}
</style>