<template>
  <div class="gameWindow"
  :style="{ 
    top:`${position.top}px`, 
    left:`${position.left}px`,
    width: isSmallScreen ? '100vw' : '640px', // 在小屏幕上设置宽度为屏幕宽度
    display: gameWindowShow ? 'block' : 'none' 
  }"
  @mousedown="startDrag">
    <div class="control">
      <div class="left">
        <div class="logo">
          <img src="image/huh-cat.gif">
        </div>        
        <div class="gameName">Cat Game</div>        
      </div>
      <div class="right">
        <div class="controlButton">
          <button class="minimize" type="button" @click="clickMinButton">
            <svg xmlns="http://www.w3.org/2000/svg" height="10" width="10" viewBox="0 0 512 512"><path d="M24 432c-13.3 0-24 10.7-24 24s10.7 24 24 24l464 0c13.3 0 24-10.7 24-24s-10.7-24-24-24L24 432z"/></svg>
          </button>
        </div>
        <div class="controlButton close">
          <button type="button">
            <svg xmlns="http://www.w3.org/2000/svg" height="10" width="7.5" viewBox="0 0 384 512"><path d="M376.6 84.5c11.3-13.6 9.5-33.8-4.1-45.1s-33.8-9.5-45.1 4.1L192 206 56.6 43.5C45.3 29.9 25.1 28.1 11.5 39.4S-3.9 70.9 7.4 84.5L150.3 256 7.4 427.5c-11.3 13.6-9.5 33.8 4.1 45.1s33.8 9.5 45.1-4.1L192 306 327.4 468.5c11.3 13.6 31.5 15.4 45.1 4.1s15.4-31.5 4.1-45.1L233.7 256 376.6 84.5z"/></svg>
          </button>
        </div>        
      </div>
    </div>

    <div class="Options">
      <div>File</div>
      <div>Options</div>
      <div>Windows</div>
      <div>Help</div>
    </div>

    <div class="AllGameValue">
      <div class="toolValue">
        <div class="nameBox whiteBox">名稱:貓咪</div>
        <div class="buttonBox" v-if="WhatTime.day !== 8">
          <div 
          v-if="WhatTime.currentTime === 'breakfast' || 
          WhatTime.currentTime === 'lunch' ||
          WhatTime.currentTime === 'dinner'
          ">
            <button class="" @click="feedFood">貓糧</button>
            <button @click="feedCanned">貓罐頭</button>
            <button @click="feedWater">水</button>
            <button @click="feedMedicine" v-if="catState.energy==='生病'">藥</button>
          </div>

          <div 
          v-if="WhatTime.currentTime === 'morning' ||
          WhatTime.currentTime === 'afternoon' ||
          WhatTime.currentTime === 'night'
          ">
            <button 
            @click="TakeANap" 
            v-if="WhatTime.currentTime === 'afternoon'">
              睡午覺
            </button>
            <button 
            @click="TakeAWalk"
            v-if="WhatTime.currentTime === 'morning' ||
            WhatTime.currentTime === 'afternoon'">
              帶去散步
            </button>
            <button @click="playWithIt">陪牠玩</button>
            <button @click="Grooming">幫牠理毛</button>
            <button 
            @click="watchTV"
            v-if="WhatTime.currentTime === 'night'">
              看電視
            </button>
            <button 
            @click="readStories"
            v-if="WhatTime.currentTime === 'night'">
              念故事
            </button>
            <button @click="slapIt">欺負牠</button>
          </div>

          <div  v-if="WhatTime.currentTime === 'bath'">
            <button @click="washIt">洗澡</button>
            <button @click="DontWashIt">不洗澡</button>
          </div>

          <div v-if="WhatTime.currentTime ==='sleep'">
            <button @click="letCatSleep">叫牠睡覺</button>
            <button @click="doNotLetCatSleep">讓牠自由活動</button>
          </div>

          <div v-if="WhatTime.currentTime === 'calculateThisDay'">
            <button @click="goToNextDay">結束這一天</button>
          </div>
        </div>
        <div class="buttonBox" v-if="WhatTime.day == 8">
          <div>
            <button @click="restartButton">重新開始</button>
          </div>

        </div>
      </div>

      <div class="gameValue">
        <div class="AllState">
          <h1 class="greyBox">狀態</h1>
          <div class="whiteBox">
            <span>日期:第{{ WhatTime.day }}天</span>
            <span>時間:{{ WhatTimeNow() }}</span>
            <span>外觀:看起來很{{ catState.state }}</span>
            <span>身體:{{ catState.cleanValue }}</span>
          </div>
        </div>
        <div class="catValue">
          <h1 class="greyBox"></h1>
          <div></div>
          <div class="whiteBox">
            <div>
              <div v-if="WhatTime.day !== 8">
                {{ catState.respond }}
              </div>
              <div  v-if="WhatTime.day === 8">
                一周過去，你的貓成為了{{ whatCatYouHave() }}
              </div>
            </div>              
            <div class="imageBox">
              <img v-if="WhatTime.day !== 8" src="image/就是貓.jpg" >
              <img v-if="WhatTime.day === 8" :src="whatCatImgURL()" >
            </div>              
          </div>
        </div>
      </div>
      
      <div class="Info">
        <div>
          <span>飽足感:{{ catState.food }}</span>
          <span>心情:{{ catState.feel }}</span>
          <span>疲勞感:{{ catState.sleep }}</span>
        </div>
      </div>    
    </div>

  </div>
</template>

<script>
import { onMounted, onUnmounted, reactive, ref, watch } from 'vue';

export default {
  props:{
    catGameShow:{
      type: Boolean,
      default:true,
    }
  },
  setup(props,{emit}){
    const catState = reactive({
      food:50,
      feel:50,
      sleep:50,
      state:'正常',
      energy:'健康',
      cleanValue:'乾淨',
      clean:7,
      respond:'這是隻普通的貓',
      MAXfood:150,
      MAXfeel:150,
      MAXsleep:150,

      rareFailEffect:3,
      normalFailEffect:5,
      superFailEffect: 8,
      rareSuccessEffect:98,
      normalSuccessEffect:95,
      superSuccessEffect:93,

      recoverHealth:0,
      demon:0,
      noSleep:0,
    });

    const getRandomNumber = ()=>{
      return Math.floor(Math.random()*101);
    }
    const getThreeRandomNumberForPlay = ()=>{
      return Math.floor(Math.random()*3);
    }
    const getRandomNumberInFive = ()=>{
      return Math.floor(Math.random()*3);
    }
    const getRandomNumberForSleepEvent = ()=>{
      return Math.floor(Math.random()*3);
    }

    //乾淨值計算
    const howClean = ()=>{
      if(catState.clean < 1){
        catState.cleanValue  = '黑色毛炭';
      } else if(catState.clean >=5){
        catState.cleanValue  = '乾淨';
      } else if(catState.clean >=3){
        catState.cleanValue  = '普通';
      } else if(catState.clean <=1){
        catState.cleanValue  = '骯髒';
      }
    }

    //隨機反應
    const whatFeedNormalReaction = ()=>{
      const randomNumberInFive = getRandomNumberInFive();
      if(randomNumberInFive === 0){
        return '在你弄飼料時牠在旁邊靜靜的看著，接著';
      } else if ( randomNumberInFive === 1){
        return '儘管你對牠滿滿的愛但牠眼中只有飼料，';
      } else if ( randomNumberInFive === 2){
        return '吃飯前牠對你喵喵叫了幾聲但你聽不懂牠想說甚麼，總之';
      }
    }

    const whatFeedSuccessReaction = ()=>{
      const randomNumberInFive = getRandomNumberInFive();
      if(randomNumberInFive === 0){
        return '在你弄飼料時電話響起，你一轉頭功夫，';
      } else if ( randomNumberInFive === 1){
        return '牠突然變成比你還要巨大，當你傻在原地時，';
      } else if ( randomNumberInFive === 2){
        return '牠指著左邊說"看那邊喵，你"蛤"了一聲轉過頭去，';
      }
    }

    const whatFeedFailReaction = ()=>{
      const randomNumberInFive = getRandomNumberInFive();
      if(randomNumberInFive === 0){
        return '牠突然打翻自己的碗，';
      } else if ( randomNumberInFive === 1){
        return '當你弄完飼料卻看到牠另在那一動不動，你搖了搖牠還是不動，幾分鐘後牠';
      } else if ( randomNumberInFive === 2){
        return '牠把飼料拿去給隔壁的狗吃，途中';
      }
    }

    const whatFeedSickReaction = ()=>{
      const randomNumberInFive = getRandomNumberInFive();
      if(randomNumberInFive === 0){
        return '牠病懨懨的，吃不下任何東西';
      } else if ( randomNumberInFive === 1){
        return '牠毫無食慾，趴在窩裡看起來很難受';
      } else if ( randomNumberInFive === 2){
        return '牠半睜開的眼看著你，似乎想告訴你應該為牠吃藥';
      }
    }

    const whaWaterReaction = ()=>{
      const randomNumberInFive = getRandomNumberInFive();
      if(randomNumberInFive === 0){
        return '牠一臉嫌棄地把它喝了';
      } else if ( randomNumberInFive === 1){
        return '牠喝了幾口"呸"了一聲走掉了';
      } else if ( randomNumberInFive === 2){
        return '牠舔了口就走掉了';
      }
    }

    const whatPlaySickReaction = ()=>{
      const randomNumberInFive = getRandomNumberInFive();
      if(randomNumberInFive === 0){
        return '牠病懨懨的，在原地翻來翻去';
      } else if ( randomNumberInFive === 1){
        return '牠閉上眼在原地呻吟著，你看著都覺得心痛';
      } else if ( randomNumberInFive === 2){
        return '牠半睜開的眼看著你，似乎想告訴你牠需要休息';
      }
    }

    //限制最大值
    const MaxValue = () =>{
      if(catState.food >= catState.MAXfood){
        catState.food = catState.MAXfood
        catState.respond = '牠已經比橘貓還大隻了 你是在養豬嗎?';
      }
      if(catState.feel >= catState.MAXfeel){
        catState.feel = catState.MAXfeel
      }
      if(catState.sleep >= catState.MAXsleep){
        catState.sleep = catState.MAXsleep
      }
    }

    //早午晚餐
    const feedCat = (successProbability, failProbability, foodEffect, feelEffect, sleepEffect, responses)=>{
      if(catState.energy === '生病'){
        catState.respond = whatFeedSickReaction();
      } else {
        const randomNumber = getRandomNumber();
        if(catState.food < catState.MAXfood){
          if(randomNumber >= successProbability){
            catState.food += foodEffect.success;
            catState.feel += feelEffect.success;
            catState.respond = whatFeedSuccessReaction() + responses.success;
          } else if(randomNumber <= failProbability){
            catState.feel += feelEffect.fail;
            catState.respond = whatFeedFailReaction() + responses.fail;
            catState.clean -= 1;
          } else {
            catState.food += foodEffect.normal;
            catState.feel += feelEffect.normal;
            catState.respond = whatFeedNormalReaction() + responses.normal;
          }        
        }
        //睡眠設定
        if(catState.sleep >= catState.MAXsleep){
          catState.food += 0;
          catState.feel += 0;
          catState.sleep = catState.MAXsleep;
          catState.respond = responses.sleep;
        } else {
          catState.sleep += sleepEffect;
        }
      }
      MaxValue();
      Howstate();
      storyContinue();
    }

    const feedFood = ()=>{
      feedCat(
        catState.rareSuccessEffect,
        catState.rareFailEffect,
        { success: 16, normal: 8 },
        { success: 2, fail:2, normal: 1},
        3,
        {
          success: '牠衝進櫃子把另一包飼料全吃了',
          fail: '把飼料灑得滿地',
          normal: '牠吃得很開心',
          sleep: '牠累得像條狗，在你弄飼料期間就睡著了',
        }
      )
    }

    const feedCanned = ()=>{
      feedCat(
        catState.normalSuccessEffect,
        catState.normalFailEffect,
        { success: 8, normal: 6 },
        { success: 2, fail:2, normal: 1 },
        3,
        {
          success: '牠立刻打開了另一個罐頭吃了',
          fail: '把罐頭打翻並將裡面的肉泥沾得滿身都是',
          normal: '牠吃得很開心',
          sleep: '牠累得像條狗，在你弄飼料期間就睡著了',
        }
      )
    }

    const feedWater = ()=>{
      if(catState.energy === '生病'){
        catState.respond = whatFeedSickReaction();
      } else {
        if(catState.food >= 100){
          catState.food -= 5;
          catState.feel -= 2;
          catState.respond = '由於他太胖又看起來不會餓，所以你給了牠碗水，' + whaWaterReaction();
        } else if(catState.food <= -50){
          catState.respond = '兄弟，你是在養貓還是養鬼';
          catState.food = -50;
        } else {
          catState.food -= 3;
          catState.feel -= 1;
          catState.respond = '你給了牠碗水，' + whaWaterReaction();
          if(catState.food <= -50){
            catState.food = -50;
          }
        }
      }
      storyContinue();
    }

    const feedMedicine = ()=>{
      const recoverNumber = getRandomNumberInFive();
      catState.recoverHealth += recoverNumber;
      if(catState.recoverHealth >=5){
        catState.respond = '回復活力';
        catState.energy = '健康'
        catState.feel += 15;
        catState.recoverHealth = 0;
      } else {
        catState.respond = '還是生病';
        catState.feel -= 1;
        catState.sleep += 5;
      }
      storyContinue();
    }

    //活動事件
    const playCat = (foodEffect, feelEffect, sleepEffect, responses)=>{
      if(catState.energy === '健康'){
        const randomNumber = getThreeRandomNumberForPlay();
        if(randomNumber === 0){
          catState.respond = responses.one;
          catState.feel += feelEffect.one;
          catState.food -= foodEffect.one;
          catState.sleep += sleepEffect.one;
        } else if(randomNumber === 1){
          catState.respond = responses.two;
          catState.feel += feelEffect.two;
          catState.food -= foodEffect.two;
          catState.sleep += sleepEffect.two;
        } else if(randomNumber === 2){
          catState.respond = responses.three;
          catState.feel += feelEffect.three;
          catState.food -= foodEffect.three;
          catState.sleep += sleepEffect.three;
        }
      } else if (catState.energy === '生病'){
        catState.respond = whatPlaySickReaction();
        catState.feel -= 5;
        catState.food -= 2;
        catState.sleep += 5;
      }
      MaxValue();
      Howstate();
      howClean();
      storyContinue();
    }

    const TakeANap = ()=>{
      if(catState.energy === '健康'){
        const randomNumber = getThreeRandomNumberForPlay();
        if(randomNumber === 0){
          catState.respond = "牠翻過來躺在地上，睡到流口水，看起來真的很舒服";
        } else if(randomNumber === 1){
          catState.respond = "牠趴在你電腦鍵盤上呼呼大睡，看來是不打算讓你下午好過了";
        } else if(randomNumber === 2){
          catState.respond = "牠乖乖地在自己的窩裡睡覺，這行為讓你訝異的以為牠吃壞肚子了";
        }
        catState.feel += 10;
        catState.food -= 2;
        catState.sleep -= 5;
      } else if (catState.energy === '生病'){
        catState.respond = "在生病的時候還是好好休息吧";
        catState.feel += 1;
        catState.food -= 2;
        catState.sleep -= 5;
      }
      MaxValue();
      storyContinue();
    }

    const TakeAWalk = ()=>{
      catState.clean -=2;
      playCat(
        { one: 3, two: 3, three: 5 },
        { one: 3, two: 4, three:10 },
        { one: 3, two: 3, three: 7 },
        {
          one:'你帶著牠在附近溜搭，牠很乖配合。回到家你發現牠只撿了一隻路邊的小蟲回來',
          two:'你帶著牠去了公園。牠在那蹦蹦跳跳玩得全身是沙，你想著晚上該給牠洗澡了',
          three:'你帶著牠去公園溜搭，到了那發現公園只有你們，牠經歷了整個公園都是自己遊樂場的一段美好時光'
        }
      )
    }

    const playWithIt = ()=>{
      catState.clean -=1;
      playCat(
        { one: 3, two: 3, three: 5 },
        { one: 4, two: 4, three: 18 },
        { one: 5, two: 5, three: 9 },
        {
          one:'你拿了個綁著毛的棒子在牠面前揮舞，牠跟著搖了搖頭，接著把逗貓幫抓了起來',
          two:'你拿雷射筆在地上照了個紅點，牠追著紅點不亦樂乎',
          three:'你把一隻玩具老鼠丟給牠，但牠反應有點大，將這隻玩具老鼠殺了，弄得滿地棉花屑'
        }
      )
    }

    const Grooming = ()=>{
      playCat(
        { one: 2, two: 2, three: 2 },
        { one: 3, two: 3, three: 6 },
        { one: 2, two: 2, three: 2 },
        {
          one:'你用手了摸牠的毛，牠很舒服的給你理毛',
          two:'你把牠抱到腿上摸了摸牠，牠一臉"你在幹甚麼"的看著你',
          three:'由於你高超的撫摸技巧，牠舒服地發出咕嚕咕嚕'
        }
      )      
    }

    const watchTV = ()=>{
      playCat(
        { one: 1, two: 1, three: 1 },
        { one: 2, two: 2, three: 3 },
        { one: 2, two: 2, three: 2 },
        {
          one:'你打開電視撥放最愛的節目，牠在一旁跟著你一起看',
          two:'你打開電視撥放最愛的節目，由於牠不感興趣所以一直在煩你',
          three:'在你要打開電視前一刻，牠搶過遙控器並轉到動物星球頻道，牠一臉愉悅地看著你'
        }
      )   
    }

    const readStories = ()=>{
      playCat(
        { one: 1, two: 1, three: 1 },
        { one: 3, two: 3, three: 6 },
        { one: 2, two: 2, three: 2 },
        {
          one:'你講了"睡美人"的故事給牠聽，但牠不感興趣睡著了',
          two:'你講了"白雪公主"的故事給牠聽，但牠似乎不感興趣',
          three:'你講了"鞋貓劍客"的故事給牠聽，之後牠興奮的跑進你的靴子裡流口水'
        }
      )
    }

    const slapIt = ()=>{
      catState.demon +=1;
      playCat(
        { one: 1, two: 1, three: 1 },
        { one: 15, two: 15, three: 15 },
        { one: -5, two: -5, three: -5 },
        {
          one:'你舉起手故意拍牠，在你手拍到牠前，你的頭被牠手中的槍指著，"救命喔，牠想要我死"',
          two:'你拿起床上的枕頭想偷襲牠，卻感覺這觸感不對，一看你手中竟然拿著牠。安靜的空氣中，你的腦海中只有"我死定了"',
          three:'在你有這個念頭的同時，沒有任何警告、沒有線索、沒有任何聲音，牠直直地盯著你好像在說"你死定了"'
        }
      )
    }

    //洗澡
    const washIt = ()=>{  
      if(catState.cleanValue === '乾淨'){
        catState.respond = '儘管牠很乾淨，但你還是要給牠洗一洗';
        catState.clean += 1;
      } else if(catState.cleanValue === '普通'){
        catState.respond = '你把牠洗了洗';
        catState.clean = 7;
      } else if(catState.cleanValue === '骯髒'){
        catState.respond = '你把牠又洗又刷';
        catState.clean = 7;
      } else if(catState.cleanValue === '黑色毛炭'){
        catState.respond = '"這是神影少女裡面的煤炭球嗎?"你心裡這麼想著同時用力地洗刷這團黑球';
        catState.clean = 7;
      }
      storyContinue();
    }

    const DontWashIt = ()=>{   
      if(catState.cleanValue === '乾淨'){
        catState.respond = '牠看起來很乾淨 不需要洗澡';
      } else if(catState.cleanValue === '普通'){
        catState.respond = '你把牠拿起來聞了聞，一股貓味但不臭，所以你決定今天不幫牠洗澡';
      } else if(catState.cleanValue === '骯髒'){
        catState.respond = '儘管牠聞起來比臭臭泥還臭，但你懶得幫牠洗澡，任由牠繼續臭下去';
      } else if(catState.cleanValue === '黑色毛炭'){
        catState.respond = '"這東西繼續放著不知道會部會變成其他生物"你這樣想著';
      }  
      storyContinue();
    }

    //時間處理
    const WhatTime = reactive({
      day: 1,
      time: {
        breakfast: '早餐時間',
        morning: '早上',
        lunch: '午餐時間',
        afternoon: '下午',
        bath: '洗澡時間',
        dinner: '晚餐時間',
        night: '晚上',
        sleep: '睡覺時間',
        calculateThisDay:'睡覺時間'
      },
      currentTime: 'breakfast'  // 新增屬性來儲存當前時間段
    });

    const timeOrder = [
                        'breakfast', 
                        'morning', 
                        'lunch', 
                        'afternoon', 
                        'bath', 
                        'dinner', 
                        'night', 
                        'sleep',
                        'calculateThisDay'
                      ];

    const WhatTimeNow = () => {
      return WhatTime.time[WhatTime.currentTime];
    };

    const storyContinue = () => {
      const currentIndex = timeOrder.indexOf(WhatTime.currentTime);
      if (currentIndex === timeOrder.length - 1) {
        // 如果已經是 "睡覺時間"，則將天數加 1，並重置為 "早餐時間"
        WhatTime.day += 1;
        WhatTime.currentTime = 'breakfast';
      } else {
        // 否則進入下一個時間段
        WhatTime.currentTime = timeOrder[currentIndex + 1];
      }
    };

    //睡覺事件
    const letCatSleep = ()=>{
      if(WhatTime.currentTime ==='sleep'){
        if(catState.sleep >= 60){
          const randomNumber = getRandomNumberForSleepEvent();
          if(randomNumber === 0){
            catState.respond = '半夜牠突然在你身上吐貓毛，溼溼熱熱的感覺把你弄醒，在你睡眼惺忪處理著牠的嘔吐物時，你看到牠爬到你枕頭上睡覺，你心裡暗罵"這可惡的小王八蛋"'
          } else if(randomNumber === 1){
            catState.respond = '牠在你熟睡之後爬到你身上睡覺，雖然你一覺到天亮但卻做了個被大石頭壓著的夢'
          } else if(randomNumber === 2){
            catState.respond = "牠乖乖地躺在你身邊睡覺並做了個吃雞腿的夢，早上起來你的臉頰和枕頭都是牠的口水"
          }
        } else {
          catState.respond = "牠乖乖地跑回自己的窩去睡覺，你也度過了熟睡的夜晚"
        }
        catState.feel +=10;
        catState.food -=2;
        catState.sleep -=15;
      }
      MaxValue();
      storyContinue();
    }

    const doNotLetCatSleep = ()=>{
      const randomNumber = getRandomNumberForSleepEvent();
      if(randomNumber === 0){
        catState.respond = '牠在你睡覺的時候跑出去跟其他貓打架，並成為今晚的貓擂台冠軍'
        catState.feel +=15;
        catState.food -=5;
        catState.sleep +=7;
      } else if(randomNumber === 1){
        catState.respond = '牠在夜晚期協助外星人研發外星科技，為宇宙共同文明盡一份力，也因此取得了榮譽外星公民身分'
        catState.feel +=12;
        catState.food -=8;
        catState.sleep +=12;
      } else if(randomNumber === 2){
        catState.respond = "貓界編輯打電話來催牠稿，牠只好把今晚時間都用在牠的連載漫畫上"
        catState.feel +=8;
        catState.food -=5;
        catState.sleep +=7;
      }
      catState.noSleep += 1;
      MaxValue();
      storyContinue();
    }

    const goToNextDay = ()=>{
      catState.respond = '';
      storyContinue();
    }

    //狀態
    const Howstate = ()=>{
      if(catState.food >= 100){
        catState.state = '牠吃得像隻豬';
      } 
    }
    
    //結算
    const whatCatYouHave = ()=>{
      if(catState.demon >=12){
        return '殺手貓'
      } else if ( catState.clean >=10){
        return '潔癖貓'
      } else if (catState.noSleep >=6){
        return '熬夜貓'
      } else if ( catState.sleep >=150 && catState.feel >=150){
        return '死靈貓'
      } else if ( catState.food >=100){
        return '大胖貓'
      } else if ( catState.food <=10){
        return '骷髏弓箭貓'
      } else if ( catState.feel >=130){
        return '活力貓'
      } else if ( catState.sleep >=150){
        return '嗜睡貓'
      } else if (catState.energy === '健康' && catState.feel >= 80 && catState.sleep >= 60){
        return '健康貓'
      } else {
        return '快樂貓'
      }
    }

    const whatCatImgURL = ()=>{
      if(whatCatYouHave() === '殺手貓'){
        return 'image/殺手貓.jpg'
      } else if(whatCatYouHave() === '潔癖貓'){
        return 'image/潔癖貓.gif'
      } else if(whatCatYouHave() === '熬夜貓'){
        return 'image/熬夜貓.jpg'
      } else if(whatCatYouHave() === '死靈貓'){
        return 'image/死靈貓.jpg'
      } else if(whatCatYouHave() === '大胖貓'){
        return 'image/大胖貓.jpg'
      } else if(whatCatYouHave() === '骷髏弓箭貓'){
        return 'image/骷髏弓箭貓.jpg'
      } else if(whatCatYouHave() === '活力貓'){
        return 'image/活力貓.jpg'
      } else if(whatCatYouHave() === '嗜睡貓'){
        return 'image/嗜睡貓.jpg'
      } else if(whatCatYouHave() === '健康貓'){
        return 'image/健康貓.jpg'
      } else if(whatCatYouHave() === '快樂貓'){
        return 'image/快樂貓.jpg'
      }
    }
    //重新開始按鈕
    const resetGame = () => {
      // 重置貓的狀態
      catState.food = 50;
      catState.feel = 50;
      catState.sleep = 50;
      catState.state = '正常';
      catState.energy = '健康';
      catState.cleanValue = '乾淨';
      catState.clean = 7;
      catState.respond = '這是隻普通的貓';
      catState.demon = 0;
      catState.noSleep = 0;
      catState.recoverHealth = 0;

      // 重置遊戲的時間
      WhatTime.day = 1;
      WhatTime.currentTime = 'breakfast';
    };
    //重新開始
    const restartButton = ()=>{
      resetGame();
    }

    //控制視窗
    const position = ref({ top:100, left:350 });
    const dragging = ref(false);
    const offset = ref({ x:0, y:0 });
    const isSmallScreen = ref(false); 
    
    // 是否螢幕寬度小於 700px    
    const checkScreenSize = () => {
      isSmallScreen.value = window.innerWidth < 768;
      if (isSmallScreen.value) {
        position.value = {
          top: window.innerHeight / 3, // 居中显示
          left: 0, // 左对齐
        };
      }
    };

    //開始移動
    const startDrag = (event)=>{
    if(!event.target.closest('.control')){
      return;
    }
      if (isSmallScreen.value) return;
      dragging.value= true;
      offset.value= {
        x:event.clientX - position.value.left,
        y:event.clientY - position.value.top,
      };
      window.addEventListener('mousemove', onDrag);
      window.addEventListener('mouseup', stopDrag);
    }
    //計算移動位置
    const onDrag = (event)=>{
      if(!dragging.value) return;

      const parentRect = document.querySelector('.backgronddIV').getBoundingClientRect();
      const gameWindowRect = document.querySelector('.gameWindow').getBoundingClientRect();

      // 限制范围
      const minX = parentRect.left;
      const maxX = parentRect.right - gameWindowRect.width;
      const minY = parentRect.top;
      const maxY = parentRect.bottom - gameWindowRect.height - 36;

      const newLeft = Math.min(Math.max(event.clientX - offset.value.x, minX), maxX);
      const newTop = Math.min(Math.max(event.clientY - offset.value.y, minY), maxY);

      position.value = {
        left: newLeft,
        top: newTop,
      }
    }
    //停止移動
    const stopDrag = ()=>{
      dragging.value = false;
      window.removeEventListener('mousemove', onDrag);
      window.removeEventListener('mouseup', stopDrag);
    }

    onMounted(() => {
      checkScreenSize();
      window.addEventListener('resize', checkScreenSize);
    });

    onUnmounted(() => {
      window.removeEventListener('resize', checkScreenSize);
    });

    //最小化按鈕互動
    const clickMinButton = ()=>{
      emit('minimize');
    }
    const gameWindowShow = ref(true)
    watch(()=> props.catGameShow,(newVule)=>{
      if(newVule){
        // console.log('true');
      }else{
        // console.log('false');
      }
      gameWindowShow.value = !gameWindowShow.value;
    })
    

    
    

    return{
      catState,

      feedFood,
      feedCanned,
      feedWater,
      feedMedicine,

      TakeANap,
      TakeAWalk,
      playWithIt,
      Grooming,
      watchTV,
      readStories,
      slapIt,

      washIt,
      DontWashIt,

      WhatTime,
      WhatTimeNow,

      letCatSleep,
      doNotLetCatSleep,
      goToNextDay,

      whatCatYouHave,
      whatCatImgURL,
      restartButton,

      position,
      startDrag,
      isSmallScreen,

      clickMinButton,
      gameWindowShow,

    }
  }

}
</script>

<style lang="scss" scoped>
*{
  padding: 0;
}
$normalWindowColor:#c0c7c8;
$hoverWindowColor:#dcdcdc;
$borderColor:#87888f;
$Win95Blue:#0000a8;

.gameWindow::after{  
  position: absolute;
  content: '';
  top: -2px;
  left: -2px;
  right: -2px;
  bottom: -2px;
  border-top: 1px solid $normalWindowColor;
  border-left: 1px solid $normalWindowColor;
  border-bottom: 1px solid #000;
  border-right: 1px solid #000;
  z-index: -1;
}

.gameWindow{  
  position: relative;
  display: block;
  background-color: $normalWindowColor;
  padding: 2px;
  width: 640px;
  height: auto;
  box-sizing: border-box;
  border-top: 1px solid #fff;
  border-left: 1px solid #fff;
  border-bottom: 1px solid $borderColor;
  border-right: 1px solid $borderColor;

  .control{
    display: flex;
    height: 28px;
    justify-content: space-between;
    align-items: center;
    background-color: $Win95Blue;

    .left{
      display: flex;
      margin: 4px;
      box-sizing: border-box;

      .logo{
        height: 18px;
        width: 18px;
        margin-right: 4px;

        img{
          height: 100%;
          width: 100%;
          object-fit: cover;
        }
      }
      .gameName{
        font-weight: bolder;
        color: #fff;
      }
    }
    .right{
      display: flex;
      margin: 4px;
      box-sizing: border-box;

      .close{
          margin-left: 4px;
        }              
      .controlButton{
        
        button{
          height: 18px;
          width: 18px;
        }
      }
    }
  }
  .Options::after{
    position: absolute;
    background-color: #fff;
    height: 1px;
    width: 100%;
    content: '';
    bottom: -2px;
  }
  .Options{
    position: relative;
    display: flex;
    column-gap: 2px;
    border-bottom: 1px solid $borderColor;
    margin-bottom: 4px;

    div{
      height: 19px;
      padding: 2px 4px;
      cursor: pointer;
      margin: 2px 0;
    }
    div:hover{
      background-color: $hoverWindowColor;
    }
    div:active{
      background-color: $Win95Blue;
      color: #fff;
    }
    div::first-letter{
      display: inline-block;
      border-bottom: 1.4px solid;
    }
  }
  .AllGameValue{
    display: flex;
    flex-direction: column;
    row-gap: 3px;

    .toolValue{
      height: 28px;
      display: flex;
      justify-content: space-between;
      margin: 1px 1px;
      column-gap: 10px;

      .nameBox{
        width: 40%;
        position: relative;
        z-index: 0;
      }
      .nameBox::after{
        position: absolute;
        content: '';
        top: -2px;
        left: -2px;
        right: -2px;
        bottom: -2px;
        border-top: 1px solid $borderColor;
        border-left: 1px solid $borderColor;
        border-bottom: 1px solid #fff;
        border-right: 1px solid #fff;
        bottom: -2px;
        z-index: -1;
      }
      .buttonBox{
        margin-top: -1px;
        width: 60%;
        padding-left: 8px;
        box-sizing: border-box;

        div{
          display: flex;
          column-gap: 1px;
          height: 29px;

          button{
            padding: 0 4px;
          }
        }
      }
    }
    .gameValue{
      display: flex;
      justify-content: space-between;
      column-gap: 4px;

      .AllState{
        display: flex;
        width: 40%;
        flex-direction: column;

        h1{
          width: 100%;
          height: 32px;
          font-weight: normal;
          font-size: 16px;
          align-content: center;
          display: flex;
          padding: 4px;
          margin: 0;
          box-sizing: border-box;
        }
        .whiteBox{
          display: flex;
          min-height: 200px;
          flex-direction: column;
        }
      }
    }
    .catValue{
      display: flex;
      width: 60%;
      flex-direction: column;

      h1{
        width: 100%;
        height: 32px;
        font-weight: normal;
        font-size: 16px;
        display: flex;
        padding: 4px;
        margin: 0;
        box-sizing: border-box;
      }
      .whiteBox{
        display: flex;
        min-height: 200px;
        flex-direction: column;
        justify-content: space-between;

        .imageBox{
          width: 150px;
          height: 150px;            

          img{
            width: 100%;
            height: 100%;
            object-fit: cover;
          }
        }
      }
    }
    .Info{
    
      div{
        display: flex;
        height: 34px;
        column-gap: 4px;

        span{
          display: flex;
          border-top: 1px solid #87888f;
          border-left: 1px solid #87888f;
          border-bottom: 1px solid #fff;
          border-right: 1px solid #fff;
          padding: 4px;
          content:'';
        }
      }      
    }
  }    
}

@media (max-width:768) {
  .gameWindow {
    width: 100vw;
    left: 0;
    transform: none;
    max-width: 100%; // 确保在小屏幕时宽度不会溢出
    max-height: 100%; // 确保高度不会溢出
    overflow: hidden; // 避免内容溢出
    box-sizing: border-box;
  }  
}
</style>